import React, { useEffect } from "react";
// import { SnsLoginModule } from "oauth_sns_js/dist/main.bundle";

const SnsLoginModule = require("oauth_sns_js/dist/main.bundle");

function FacebookLoginButton() {
  const snsLoginModule = new SnsLoginModule.SnsLoginModule();

  useEffect(() => {
    const infoText = document.getElementById("info_text");
    facebookOptions.options.element = infoText;
    snsLoginModule.setSnsSDK(snsLoginModule.FACEBOOK, facebookOptions);
  });

  const POST_URL = "http://211.254.214.31:8000/api/users/create/";
  const GET_URL = "http://211.254.214.31:8000/api/users";

  const facebookOptions = {
    appId: "943575639457341",
    options: {
      version: "v8.0",
    },
    serverUrl: {
      post: POST_URL,
      get: GET_URL,
    },
  };

  function initFacebookLogin() {
    const naverButton = document.getElementById("naver");
    const kakaoButton = document.getElementById("kakao");
    const facebookButton = document.getElementById("facebook");
    const googleButton = document.getElementById("google");

    if (snsLoginModule.getCookie("isLoggedIn") === "true") {
      snsLoginModule.logout();
      naverButton.style.display = "inline";
      kakaoButton.style.display = "inline";
      googleButton.style.display = "inline";
      facebookButton.innerHTML = `<img src=${require("../images/SNS_facebook.png")} alt='facebook login button' />LOGIN`;
    } else {
      snsLoginModule.login();
      naverButton.style.display = "none";
      kakaoButton.style.display = "none";
      googleButton.style.display = "none";
      facebookButton.innerHTML = `<img src=${require("../images/SNS_facebook.png")} alt='facebook login button' />LOGOUT`;
    }
  }

  return (
    <div className="facebookButton">
      <button id="facebook" type="button" onClick={initFacebookLogin}>
        <img
          src={require("../images/SNS_facebook.png")}
          alt="facebook login button"
        />
        LOGIN
      </button>
    </div>
  );
}

export default FacebookLoginButton;
