import React, { useEffect } from "react";
// import { SnsLoginModule } from "oauth_sns_js/dist/main.bundle";

const SnsLoginModule = require("oauth_sns_js/dist/main.bundle");

function KakaoLoginButton() {
  const snsLoginModule = new SnsLoginModule.SnsLoginModule();

  useEffect(() => {
    const infoText = document.getElementById("info_text");
    kakaoOptions.options.element = infoText;
    snsLoginModule.setSnsSDK(snsLoginModule.KAKAO, kakaoOptions);
  });

  const POST_URL = "http://211.254.214.31:8000/api/users/create/";
  const GET_URL = "http://211.254.214.31:8000/api/users";

  const kakaoOptions = {
    appId: "464cadd9d18cb7f47435a5befd7be40a",
    options: {},
    serverUrl: {
      post: POST_URL,
      get: GET_URL,
    },
  };

  function initKakaoLogin() {
    const naverButton = document.getElementById("naver");
    const kakaoButton = document.getElementById("kakao");
    const facebookButton = document.getElementById("facebook");
    const googleButton = document.getElementById("google");

    if (snsLoginModule.getCookie("isLoggedIn")) {
      snsLoginModule.logout();
      naverButton.style.display = "inline";
      facebookButton.style.display = "inline";
      googleButton.style.display = "inline";
      kakaoButton.innerHTML = `<img src=${require("../images/SNS_kakao.png")} alt='kakao login button' />LOGIN`;
    } else {
      snsLoginModule.login();
      naverButton.style.display = "none";
      facebookButton.style.display = "none";
      googleButton.style.display = "none";
      kakaoButton.innerHTML = `<img src=${require("../images/SNS_kakao.png")} alt='kakao login button' />LOGOUT`;
    }
  }

  return (
    <div className="kakaoButton">
      <button id="kakao" type="button" onClick={initKakaoLogin}>
        <img
          src={require("../images/SNS_kakao.png")}
          alt="kakao login button"
        />
        LOGIN
      </button>
    </div>
  );
}

export default KakaoLoginButton;
