import React, { useEffect } from "react";
import ButtonGroupContainer from "../containers/buttonGroupContainer";
// import oauth_sns_js from "oauth_sns_js/dist/main.bundle";
import axios from "axios";
const SnsLoginModule = require("oauth_sns_js/dist/main.bundle");

function HomeView() {
  const snsLoginModule = new SnsLoginModule.SnsLoginModule();
  const GET_URL = "http://211.254.214.31:8000/api/users";

  async function getUserInfoFromServer(serverUrl, internalToken, element) {
    await axios
      .get(`${serverUrl}/${internalToken}`)
      .then((res) => {
        if (res.status === 200) {
          console.log("서버에서 성공적으로 사용자 정보를 불러왔습니다");
          element.innerText = "안녕하세요, " + res.data.user_name + " 님!";
        } else {
          console.error("서버에서 사용자 정보를 가져오지 못했습니다");
        }
      })
      .catch((e) => {
        console.error(e);
        console.error(
          "서버에서 사용자 정보를 가져오는 도중 문제가 발생했습니다"
        );
      });
  }

  useEffect(() => {
    const naverButton = document.getElementById("naver");
    const kakaoButton = document.getElementById("kakao");
    const facebookButton = document.getElementById("facebook");
    const googleButton = document.getElementById("google");
    const infoText = document.getElementById("info_text");

    if (snsLoginModule.getCookie("isLoggedIn") === "true") {
      if (snsLoginModule.getCookie("snsType") === "naver") {
        kakaoButton.style.display = "none";
        facebookButton.style.display = "none";
        googleButton.style.display = "none";
        naverButton.innerHTML = `<img src=${require("../images/SNS_naver.png")} alt='naver login button' />LOGOUT`;
      } else if (snsLoginModule.getCookie("snsType") === "kakao") {
        naverButton.style.display = "none";
        facebookButton.style.display = "none";
        googleButton.style.display = "none";
        kakaoButton.innerHTML = `<img src=${require("../images/SNS_kakao.png")} alt='kakao login button' />LOGOUT`;
      } else if (snsLoginModule.getCookie("snsType") === "facebook") {
        naverButton.style.display = "none";
        kakaoButton.style.display = "none";
        googleButton.style.display = "none";
        facebookButton.innerHTML = `<img src=${require("../images/SNS_facebook.png")} alt='facebook login button' />LOGOUT`;
      } else if (snsLoginModule.getCookie("snsType") === "google") {
        naverButton.style.display = "none";
        kakaoButton.style.display = "none";
        facebookButton.style.display = "none";
        googleButton.innerHTML = `<img src=${require("../images/SNS_google.png")} alt='google login button' />LOGOUT`;
      }
      getUserInfoFromServer(
        GET_URL,
        snsLoginModule.getCookie("internalToken"),
        infoText
      );
    }
  });

  return (
    <div className="homeView">
      <h1>auth_sns_js</h1>
      <h3 id="info_text">아래 버튼을 클릭해 SNS 로그인을 진행하세요</h3>
      <ButtonGroupContainer />
    </div>
  );
}

export default HomeView;
