import React, { useEffect } from "react";
// import { SnsLoginModule } from "oauth_sns_js/dist/main.bundle";

const SnsLoginModule = require("oauth_sns_js/dist/main.bundle");

function NaverLoginButton() {
  const snsLoginModule = new SnsLoginModule.SnsLoginModule();

  useEffect(() => {
    const infoText = document.getElementById("info_text");
    naverOptions.options.element = infoText;
    snsLoginModule.setSnsSDK(snsLoginModule.NAVER, naverOptions);
  });

  const POST_URL = "http://211.254.214.31:8000/api/users/create/";
  const GET_URL = "http://211.254.214.31:8000/api/users";
  const REDIR_URL = "http://127.0.0.1:3001";

  const naverOptions = {
    appId: "GDZbhJZerC4kkLwXTlLA",
    options: {
      redirectUrl: REDIR_URL,
    },
    serverUrl: {
      post: POST_URL,
      get: GET_URL,
    },
  };

  function initNaverLogin() {
    const naverButton = document.getElementById("naver");
    const kakaoButton = document.getElementById("kakao");
    const facebookButton = document.getElementById("facebook");
    const googleButton = document.getElementById("google");

    if (snsLoginModule.getCookie("isLoggedIn")) {
      snsLoginModule.logout();
      kakaoButton.style.display = "inline";
      facebookButton.style.display = "inline";
      googleButton.style.display = "inline";
      naverButton.innerHTML = `<img src=${require("../images/SNS_naver.png")} alt='naver login button' />LOGIN`;
    } else {
      snsLoginModule.login();
      kakaoButton.style.display = "none";
      facebookButton.style.display = "none";
      googleButton.style.display = "none";
      naverButton.innerHTML = `<img src=${require("../images/SNS_naver.png")} alt='naver login button' />LOGOUT`;
    }
  }

  return (
    <div className="naverButton">
      <button id="naver" type="button" onClick={initNaverLogin}>
        <img
          src={require("../images/SNS_naver.png")}
          alt="naver login button"
        />
        LOGIN
      </button>
    </div>
  );
}

export default NaverLoginButton;
