import React, { useEffect } from "react";
// import { SnsLoginModule } from "oauth_sns_js/dist/main.bundle";

const SnsLoginModule = require("oauth_sns_js/dist/main.bundle");

function GoogleLoginButton() {
  const snsLoginModule = new SnsLoginModule.SnsLoginModule();

  useEffect(() => {
    const infoText = document.getElementById("info_text");
    googleOptions.options.element = infoText;
    snsLoginModule.setSnsSDK(snsLoginModule.GOOGLE, googleOptions);
  });

  const POST_URL = "http://211.254.214.31:8000/api/users/create/";
  const GET_URL = "http://211.254.214.31:8000/api/users";

  const googleOptions = {
    appId:
      "1037375533881-vo3aqo623782f71rfq1c48sdbrdfdf4v.apps.googleusercontent.com",
    options: {},
    serverUrl: {
      post: POST_URL,
      get: GET_URL,
    },
  };

  function initGoogleLogin() {
    const naverButton = document.getElementById("naver");
    const kakaoButton = document.getElementById("kakao");
    const facebookButton = document.getElementById("facebook");
    const googleButton = document.getElementById("google");

    if (snsLoginModule.getCookie("isLoggedIn")) {
      snsLoginModule.logout();
      naverButton.style.display = "inline";
      kakaoButton.style.display = "inline";
      facebookButton.style.display = "inline";
      googleButton.innerHTML = `<img src=${require("../images/SNS_google.png")} alt='google login button' />LOGIN`;
    } else {
      snsLoginModule.login();
      naverButton.style.display = "none";
      kakaoButton.style.display = "none";
      facebookButton.style.display = "none";
      googleButton.innerHTML = `<img src=${require("../images/SNS_google.png")} alt='google login button' />LOGOUT`;
    }
  }

  return (
    <div className="googleButton">
      <button id="google" type="button" onClick={initGoogleLogin}>
        <img
          src={require("../images/SNS_google.png")}
          alt="google login button"
        />
        LOGIN
      </button>
    </div>
  );
}

export default GoogleLoginButton;
